import React from 'react';
import './Franchise.css';
import franchiseImg from '../../assets/images/franchiseLogo.png';
import 'bootstrap/dist/css/bootstrap.min.css';

const Franchise = () => {
    return (
        <section id='franchise'>
            <div className="container">
                <div className="row gy-5 franchise__wrap">

                    <div className="col-lg-8 col-md-12 col-sm-12 col-12">
                        <div className="franchise__info">
                            <h2>  SAJDA</h2>
                            <p className='franchise__info-p'>     ,  <br />     10     ,    7 ,        ,       .</p>
                            <p className='franchise__info-p'>        ,, , ,,  ,, ,,  .</p>
                            <p className='franchise__info-p'>      ,             .        ,     .</p>
                            {/* <button className='franchise__btn'> </button> */}
                            <a className='franchise__btn' href='#apply'> </a>
                        </div>
                    </div>

                    <div className="col-lg-4 col-md-12 col-sm-12 col-12">
                        <div className="franchise__box">
                            <img src={franchiseImg} alt="" />
                        </div>
                    </div>

                </div>
            </div>
        </section>
    );
};

export default Franchise;