import React from 'react';
import './Request.css'
import request1 from '../../assets/images/Request/request1.svg'
import request2 from '../../assets/images/Request/request2.svg'
import request3 from '../../assets/images/Request/request3.svg'

const Request= () => {
    return (
        <section id='request'>
            <div className="container">
                <h2>  </h2>
                <p className='request__p'>   :</p>

                <div className="request__info">
                    <div className="request__info1">
                        <img src={request1} alt="" />
                        <p>    500 000 ,       .</p>
                    </div>
                    <div className="request__info2">
                        <img src={request2} alt="" />
                        <p>          .</p>
                    </div>
                    <div className="request__info3">
                        <img src={request3} alt="" />
                        <p>     800       .</p>
                    </div>
                </div>
                
            </div>
        </section>
    );
};

export default Request;