import React from 'react';
import './About.css'
import aboutImg from '../../assets/images/aboutImg.svg'
import 'bootstrap/dist/css/bootstrap.min.css';

const About = () => {
    return (
        <section id='about'>
            <div className="container">
                <div className="row gy-5 about__wrap">
                    <div className="col-lg-4 col-md-4 col-sm-12 col-12 pr-3 mr-2 ">
                        <div className="about__box">
                            <img src={aboutImg} alt="" />
                        </div>
                      
                    </div>
                    <div className="col-lg-8 col-md-8 col-sm-12 col-12 pl-3 ml-2 ">
                        <div className="about__info">
                            <h2 className='with-lines'><span>  SAJDA</span></h2>

                            <p> SAJDA         ,       .</p>
                            <p>      , <br />       ,   <br />    .   "SAJDA"     ,         ,   <br />   .</p>
                            <p> "SAJDA"    2016     ,    ,   ,       .</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default About;